.App {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 100vh;
  background-color: #9194a5;
}
#display {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  font-size: 30px;
  margin: 10px;
  height: 90px;
  text-align: right;
  width: 100%;
  background-color: #2ac29a; /*#20b2ab;*/
  border-radius: 3%;
  overflow-x: hidden;
  padding-right: 10px;
  -webkit-box-shadow: inset 2px 3px 9px 1px rgba(0, 0, 0, 0.27);
  -moz-box-shadow: inset 2px 3px 9px 1px rgba(0, 0, 0, 0.27);
  box-shadow: inset 2px 3px 9px 1px rgba(0, 0, 0, 0.27);
}
#drum-machine {
  height: 400px;
  width: 360px;
  background-color: #ddddd1;
  display: flex;
  justify-content: center;
  align-items: center;
  transform: perspective(600px) rotateX(20deg);
}
#raise-display {
  height: 100px;
  margin-top: -100px;

  background-color: black;
  transform: perspective(600px) rotateX(-30deg);
  border-top: 30px solid #7a7766;
  border-left: 25px solid transparent;
  border-right: 25px solid transparent;
  border-bottom: 30px solid #7a7766;
}
.drum-pad {
  width: 80px;
  height: 80px;
  color: white;
  background-color: gray;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 10px;
}
.row {
  display: flex;
  flex-direction: row;
}
.drum-pad:active {
  transform: translate(0px, 2px);
}
#bottom {
  width: 350px;
  height: 20px;
  margin-top: 13px;
  border-top: 30px solid #7a7766;
  border-left: 25px solid transparent;
  border-right: 25px solid transparent;
}
